import { Toast } from 'mint-ui'
export default {
    data(){
        return{
            endDate: new Date(new Date().getTime() - 24*60*60*1000),        // 最大可选值日期
            pickerVisible:new Date(new Date().getTime() - 24*60*60*1000),   // 模态框日期变量
            date:{                      // 日期对象
                year    :null,
                month   :null,
                day     :null
            }
        }
    },
    methods: {
        // 日期选择确认回调
        handleConfirm(value){
            this.pickerVisible = value
            let year  = value.getFullYear()
            let month = value.getMonth() + 1
            let day   = value.getDate()
            month <= 9 ? month = '0' + month : month
            day <= 9 ? day = '0' + day : day
            this.date.year  = year
            this.date.month = month
            this.date.day   = day
        },
        // 点击显示日期选择器
        showCalendar(){
            this.$refs.picker.open()
        },
        // 获取前天或者后一天  type: 0: 前一天 1：后一天
        getBefOrAft(type,date){
            let forMatDate = date.replace(/-/g, '/')
            let d = new Date(forMatDate)
            if(type === 0){
                d = +d - 1000*60*60*24
            }else if(type === 1){
                d = +d + 1000*60*60*24  // d前面需要加上 +
            }
            d = new Date(d)
            return d
        },
        dateSelClick(state){
            if(state == 'left'){
                let leftDate = this.getBefOrAft(0,`${this.date.year}/${this.date.month}/${this.date.day}`)
                this.handleConfirm(leftDate)
            }
            if(state == 'right'){
                let rightDate = this.getBefOrAft(1,`${this.date.year}/${this.date.month}/${this.date.day}`)
                if(rightDate > new Date(new Date().getTime() - 24*60*60*1000)){
                    Toast({
                        message: '当天无数据',
                        position: 'bottom'
                    })
                    return
                }
                this.handleConfirm(rightDate)
            }
            if(state == 'yesterday'){
                this.handleConfirm(new Date())
                let leftDate = this.getBefOrAft(0,`${this.date.year}/${this.date.month}/${this.date.day}`)
                this.handleConfirm(leftDate)
            }
        }
    },
    components: {
        RadarMap: resolve => require(['@/components/echarts/RadarMap'],resolve), // 雷达图组件
        Pie: resolve => require(['@/components/echarts/Pie'],resolve),           // 饼状图组件
        LoopPie: resolve => require(['@/components/echarts/LoopPie'],resolve),   // 环形饼状图组件
        Graphs: resolve => require(['@/components/echarts/Graphs'],resolve),     // 多曲线图组件
    },
    created () {
        this.handleConfirm(this.pickerVisible)
        console.log(new Date(new Date().getTime() - 24*60*60*1000).getTime(),this.pickerVisible.getTime())
    }
}



